.InfoPage h1{
   margin-top: 10vh;
   margin-left: 5vh;
}

.workerPage{
   height: 100vh;
   width: 100vw;
   display: grid;
   place-items: center;
   background-image: url("../../public/Christmas.jpg");
   background-repeat: no-repeat;
   background-size: cover;
}
.workerPage h3{
   margin-top: 20vh;
   font-size: 4vh;
}

.workerPage .reset{
   position: fixed;
   top:33vh;
   left:10vw;
   width: 20vw;
   height: 10vh;
   font-size: 20px;
   border-radius: 50px;
   background-color: aliceblue; 
   border-width: 2px;
   border-style:outset; 
   border-color: rgba(0, 0, 0, 0.516); 
}

.workerPage button:hover{
   background-color: gray;
   color: aliceblue;  
}

.workerPage h5{
   margin-top: 0;
   margin-bottom: 25vh;
   font-size: 4vh;  
}

.workerPage button{
   margin: 7px;
   margin-bottom: 10vh;
   width: 20vw;
   height: 10vh;
   font-size: 18px;
   border-radius: 50px;
   background-color: aliceblue; 
   border-width: 2px;
   border-style:ridge; 
   border-color: rgba(0, 0, 0, 0.516); 
}

.workerPage button:hover{
   background-color: gray;
   color: aliceblue;  
}
.workerPage .check{
   display: flex;
   flex-direction: column;
   text-align: center;
 }

.workerPage .check button {
   width: 225px;
   height: 50px;
   margin: 7px;
   margin-bottom: 30vh; 
   border: none;
   border-radius: 5px;
   padding: 5px;
   font-size: 16px;
   background: lightblue;
   color: #fff;
   cursor: pointer;
 }

 .workerPage .check button:hover {
   background: rgb(98, 197, 230);
 }

 .workerPage .check input {
   width: 210px;
   height: 40px; 
   margin: 7px;
   border: 2px solid slategrey;
   border-radius: 5px;
   padding: 5px;
   font-size: 16px;
 }