.NotLogin img{
    display: flex;
    position: absolute;
    height:100%;
    width: 100%;
    /* justify-content: center;
    align-items: center; */
    opacity: 0.8;
    position: 50% 0;
    /* background-size:cover ; */
    
    z-index: -1;
}


.NotLogin {
    display: flex;
    position: absolute;
    height:100%;
    width: 100%;
    justify-content: center;
    align-items: center;
    font-size: 100px;
} 
.NotLogin .context{
    display:block;
    font-weight: 400;
    text-shadow: 1px 1px rgba(244, 240, 240, 0.8);
    font-family:fantasy,cursive;
    /* z-index : 5 */
} 
.NotLogin button{
    display: block;
    position: fixed;
    width: 150px;
    height: 50px;
    margin: 7px;
    border: none;
    border-style: double;
    border-radius: 7px;
    padding: 5px;
    font-size: 16px;
    background: rgba(255, 255, 255, 0.82);
    color: black;
    cursor: pointer;
    top:60vh;
    /* position: absolute; */
    /* z-index : 5 */
}
.NotLogin button:hover{
    background: silver
}